export function getErrorByField(
  errors: Array<{ field: string; description: string }> | null,
  field: string
): string {
  const error = errors?.find((error) => error.field === field);
  return error ? error.description : "";
}

export function fieldHasError(
  errors: Array<{ field: string; description: string }> | null,
  field: string
): boolean {
  return getErrorByField(errors, field).length > 0;
}
